import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import API_URL from '../api';

const JobDetail = ({ job }) => {
  const [formData, setFormData] = useState(job);
  const [showDeleteConfirmation, setShowDeleteConfirmation] = useState(false);
  const [showNotification, setShowNotification] = useState(false);
  const [notificationMessage, setNotificationMessage] = useState("");
  const [notificationColor, setNotificationColor] = useState("green");

  const modalRef = useRef(null);
  const modalInstance = useRef(null);

  useEffect(() => {
    if (modalRef.current) {
      modalInstance.current = new window.bootstrap.Modal(modalRef.current);
    }
    return () => {
      if (modalInstance.current) modalInstance.current.dispose();
    };
  }, []);

  const showModal = () => modalInstance.current?.show();
  const hideModal = () => modalInstance.current?.hide();

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSave = async () => {
    try {
      const formDataToSend = new FormData();
      Object.keys(formData).forEach(key => {
        if (formData[key] !== null && formData[key] !== undefined) {
          formDataToSend.append(key, formData[key]);
        }
      });
      formDataToSend.append('_method', 'PUT');

      const response = await fetch(`${API_URL}/api/jobs/${formData.id}`, {
        method: 'POST',
        body: formDataToSend,
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        }
      });

      if (!response.ok) throw new Error("Failed to update job");

      setNotificationMessage("Job updated successfully!");
      setNotificationColor("green");
      setShowNotification(true);
      setTimeout(() => {
        setShowNotification(false);
        hideModal();
        window.location.reload();
      }, 2000);
    } catch (error) {
      setNotificationMessage(error.message || "Failed to update");
      setNotificationColor("red");
      setShowNotification(true);
      setTimeout(() => setShowNotification(false), 3000);
    }
  };

  const handleDelete = async () => {
    try {
      const response = await axios.post(
        `${API_URL}/api/jobs/${formData.id}`,
        { _method: 'DELETE' },
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Content-Type': 'application/json'
          }
        }
      );
      if (response.status === 200) {
        setNotificationMessage("Job deleted!");
        setNotificationColor("green");
        setShowNotification(true);
        setTimeout(() => {
          setShowNotification(false);
          window.location.reload();
        }, 1500);
      }
    } catch (error) {
      setNotificationMessage("Delete failed.");
      setNotificationColor("red");
      setShowNotification(true);
    }
  };

  return (
    <>
      {/* Trigger Button */}
      <div className="d-flex mb-3">
        <button className="btn btn-primary btn-sm me-2" onClick={showModal}>See More</button>
        <button className="btn btn-danger btn-sm" onClick={() => setShowDeleteConfirmation(true)}>Delete</button>
      </div>

      {/* Notification */}
      {showNotification && (
        <div className={`alert ${notificationColor === "red" ? "alert-danger" : "alert-success"} alert-dismissible fade show`}>
          {notificationMessage}
          <button type="button" className="btn-close" onClick={() => setShowNotification(false)}></button>
        </div>
      )}

      {/* Modal */}
      <div className="modal fade" ref={modalRef} tabIndex="-1" aria-hidden="true">
        <div className="modal-dialog modal-dialog-centered" style={{ maxWidth: '650px', width: '95%' }}>
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title">Edit Job Details</h5>
              <button type="button" className="btn-close" onClick={hideModal}></button>
            </div>
            <div className="modal-body">
              <div className="row g-3">
                <div className="col-md-6">
                  <label className="form-label">Job Title:</label>
                  <input type="text" className="form-control" name="job_title" value={formData.job_title || ''} onChange={handleInputChange} />

                  <label className="form-label mt-2">Description:</label>
                  <textarea className="form-control" name="description" value={formData.description || ''} onChange={handleInputChange}></textarea>

                  <label className="form-label mt-2">Learner Name:</label>
                  <input type="text" className="form-control" name="learner_name" value={formData.learner_name || ''} onChange={handleInputChange} />

                  <label className="form-label mt-2">Gender:</label>
                  <select className="form-select" name="learner_gender" value={formData.learner_gender || ''} onChange={handleInputChange}>
                    <option value="">Select Gender</option>
                    <option value="male">Male</option>
                    <option value="female">Female</option>
                    <option value="other">Other</option>
                  </select>

                  <label className="form-label mt-2">Location:</label>
                  <select className="form-select" name="location" value={formData.location || ''} onChange={handleInputChange}>
                    <option value="">-- Select Location --</option>
                    <option value="Yangon">Yangon</option>
                    <option value="Mandalay">Mandalay</option>
                    <option value="NayPyiTaw">NayPyiTaw</option>
                    {/* Add others as needed */}
                  </select>
                </div>

                <div className="col-md-6">
                  <label className="form-label">Address:</label>
                  <input type="text" className="form-control" name="address" value={formData.address || ''} onChange={handleInputChange} />

                  <label className="form-label mt-2">Tutoring Mode:</label>
                  <select className="form-select" name="tutoring_mode" value={formData.tutoring_mode || ''} onChange={handleInputChange}>
                    <option value="online">Online</option>
                    <option value="in-person">In-person</option>
                    <option value="both">Both</option>
                  </select>

                  <label className="form-label mt-2">Tutor Qualification:</label>
                  <input type="text" className="form-control" name="wanted_tutor_qualification" value={formData.wanted_tutor_qualification || ''} onChange={handleInputChange} />

                  <label className="form-label mt-2">Hours Per Week:</label>
                  <input type="number" className="form-control" name="hours_per_week" value={formData.hours_per_week || ''} onChange={handleInputChange} />

                  <label className="form-label mt-2">Salary Rate (Ks):</label>
                  <input type="number" className="form-control" name="salary_rate" value={formData.salary_rate || ''} onChange={handleInputChange} />

                  <label className="form-label mt-2">Start Date:</label>
                  <input type="date" className="form-control" name="start_date" value={formData.start_date || ''} onChange={handleInputChange} />
                </div>
              </div>

              <div className="mt-3">
                <label className="form-label">Special Request:</label>
                <textarea className="form-control" name="special_request" value={formData.special_request || ''} onChange={handleInputChange}></textarea>
              </div>
            </div>
            <div className="modal-footer">
              <button className="btn btn-secondary" onClick={hideModal}>Cancel</button>
              <button className="btn btn-primary" onClick={handleSave}>Save Changes</button>
            </div>
          </div>
        </div>
      </div>

      {/* Delete Confirmation Modal */}
      {showDeleteConfirmation && (
        <div className="modal show fade d-block" tabIndex="-1">
          <div className="modal-dialog modal-dialog-centered">
            <div className="modal-content">
              <div className="modal-header"><h5 className="modal-title">Confirm Delete</h5></div>
              <div className="modal-body">Are you sure you want to delete this job?</div>
              <div className="modal-footer">
                <button className="btn btn-secondary" onClick={() => setShowDeleteConfirmation(false)}>Cancel</button>
                <button className="btn btn-danger" onClick={handleDelete}>Delete</button>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default JobDetail;
